.card {
  border: 4px rgb(12, 12, 12) solid;
  border-radius: 2px;
  width: 150px;
  height: 150px;
  margin: 1rem;
  position: relative;
  box-shadow: 4px 4px 10px rgb(56, 56, 56);
  -webkit-transition: all .2s ease-in-out;
  transition: all .2s ease-in-out;
}

.card > .img-container {
  height: 100%;
  overflow: hidden;
  background:rgb(255, 255, 255);
}

.card > .img-container > img {
  width: 100%;
}

.card:hover {
  box-shadow: 8px 8px 20px rgb(56, 56, 56);
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
  cursor: pointer;
}

.card:active {
  -webkit-transform: translate(1px, 1px);
          transform: translate(1px, 1px);
}
nav {
  width: 100%;
  font-size: 3rem;
  color:#00ffff;
  margin-bottom: 50px;
  background-color: rgba(0, 0, 0, 0.69);
  height: auto;
  font-family: 'Anton', sans-serif;
}

a {
  font-family: 'Faster One', cursive;
  color:#ff00df;
  font-size: 4rem;
  text-shadow: 8px 8px 8px rgb(3, 0, 0); 
}

a:hover {
  font-family: 'Faster One', cursive;
  color: #a900ff;
  text-decoration: none;
}

nav li {
  display: inline-block;
  padding: 26px 0;
  width: 25%;
  /* text-align: center; */
}

ul {
  /* padding-left: 0px; */
  display: flex;
  align-items: center;
}

@media only screen and (max-width: 750px) {
  #cur-sco, #top-sco {
    font-size: 2rem;
  }
}
.wrapper {
  background: rgb(175, 175, 175);
  
  background-image: url('https://i.pinimg.com/originals/88/a1/e4/88a1e48b76f0d56a4b4e06cb1ec50cbb.png');
  height: 100%;
  display: flex;
  flex-flow: row wrap;
  justify-content: space-around;
  align-content: flex-start;
  overflow: auto;
  padding-bottom: 50px;
}



.title {
  width: 100%;
  text-align: right;
  font-size: 4rem;
  color: #e2ff00;
  margin-bottom: 50px;
  /* padding-left: 3%;*/
  padding-right: 3%;  
  text-shadow: 8px 8px 8px rgb(3, 0, 0); 
}

.title {
  width: 100%;
